//tabs
const tabPrimary = document.querySelector(".tab__primary");
const tabSecondary = document.querySelector(".tab__secondary");
const tabTertiary = document.querySelector(".tab__tertiary");
// console.log(tabPrimary, tabSecondary, tabTertiary);

const bookmarkPrimary = document.querySelector(".bookmark-primary");
const bookmarkSecondary = document.querySelector(".bookmark-secondary");
const bookmarkTertiary = document.querySelector(".bookmark-tertiary");
// console.log(bookmarkSecondary);

// const openSecondTab = () => {
//   bookmarkSecondary.classList.add("translateLeft");
// };

// tabSecondary.addEventListener("click", openSecondTab);

const tabs = document.querySelectorAll(".tab");
console.log(tabs);

tabs.forEach((tab) => {
  tab.addEventListener("click", function (e) {
    if (this.classList.contains("tab__secondary")) {
      e.preventDefault();
      bookmarkPrimary.classList.add("hide");
      bookmarkSecondary.classList.add("show");
      bookmarkSecondary.classList.remove("hide");
      bookmarkTertiary.classList.add("hide");
      tabPrimary.classList.remove("borderBotRed");
      tabSecondary.classList.add("borderBotRed");
      tabTertiary.classList.remove("borderBotRed");
    } else if (this.classList.contains("tab__tertiary")) {
      e.preventDefault();
      bookmarkPrimary.classList.add("hide");
      bookmarkSecondary.classList.add("hide");
      bookmarkTertiary.classList.add("show");
      bookmarkTertiary.classList.remove("hide");
      tabPrimary.classList.remove("borderBotRed");
      tabSecondary.classList.remove("borderBotRed");
      tabTertiary.classList.add("borderBotRed");
    } else {
      e.preventDefault();
      bookmarkPrimary.classList.add("show");
      bookmarkPrimary.classList.remove("hide");
      bookmarkSecondary.classList.add("hide");
      bookmarkTertiary.classList.add("hide");
      tabPrimary.classList.add("borderBotRed");
      tabSecondary.classList.remove("borderBotRed");
      tabTertiary.classList.remove("borderBotRed");
    }
  });
});

const arrows = document.querySelectorAll(".arrow");
const questions = document.querySelectorAll(".question");
const answer = document.querySelector(".question__answer");
console.log(questions);

//!real questions
questions.forEach((question) => {
  console.log(question.children);
  question.addEventListener("click", function () {
    const [, ans] = question.children;

    //! MAKE ARROWS ROTATE ON CLICK AND CHANGE COLOR

    if (ans.classList.contains("closeDescription")) {
      ans.classList.remove("closeDescription");
      ans.classList.add("openDescription");
    } else {
      ans.classList.add("closeDescription");
      ans.classList.remove("openDescription");
    }
  });
});

//!real arrow
arrows.forEach((arrow) => {
  console.log(arrow);
  arrow.addEventListener("click", function () {
    if (arrow.classList.contains("arrowClosed")) {
      arrow.classList.add("arrowOpen");
      arrow.classList.remove("arrowClosed");
    } else {
      arrow.classList.remove("arrowOpen");
      arrow.classList.add("arrowClosed");
    }
  });
});

